import { makeStyles } from "@material-ui/core/styles"
import Switch from "@material-ui/core/Switch"
import FormControlLabel from "@material-ui/core/FormControlLabel"
import Card from "@material-ui/core/Card"
import Typography from "@material-ui/core/Typography"
import PathRecorder from "../path-recorder"
import Networking from "../networking"
import { GNSS_FIX_TYPE } from "../shared/constants"

const useStyles = makeStyles((theme) => ({
  root: {
    display: "inline-block",
    position: "absolute",
    marginLeft: theme.spacing(1.5),
    marginTop: theme.spacing(1.5),
    // boxShadow: "0 8px 40px -12px rgba(0,0,0,0.3)",
    boxShadow: "3px 3px 5px rgba(0,0,0,0.2)",
    zIndex: 1,
  },
  title: {
    margin: theme.spacing(1),
  },
  body: {
    margin: theme.spacing(1),
  },
  pathSwitch: {
    marginLeft: 0,
  },
}))

interface Props {
  pathRecorder: PathRecorder
  networking: Networking
}

export default function Component({ pathRecorder, networking }: Props) {
  const classes = useStyles()

  return (
    <Card variant="outlined" className={classes.root}>
      <Typography variant="h6" className={classes.title}>
        Recording Path
      </Typography>

      <Typography variant="body1" className={classes.body}>
        GPS Status: {GNSS_FIX_TYPE[networking.robotState.gps.status]}
        <br />
        Points Recorded: {pathRecorder.numberOfPoints}
      </Typography>

      <FormControlLabel
        className={classes.pathSwitch}
        control={
          <Switch
            checked={pathRecorder.isRecordingLine}
            onChange={() => pathRecorder.toggleLineRecording()}
            color="primary"
          />
        }
        label="Record a line"
      />
    </Card>
  )
}
