import React from "react"
import { DRAWER_WIDTH, OVERSEER_STATE_STRING } from "../shared/constants"

import AppBar from "@material-ui/core/AppBar"
import Button from "@material-ui/core/Button"
import IconButton from "@material-ui/core/IconButton"
import ErrorOutlineIcon from "@material-ui/icons/ErrorOutline"
import FiberManualRecordIcon from "@material-ui/icons/FiberManualRecord"
import Hidden from "@material-ui/core/Hidden"
import Tooltip from "@material-ui/core/Tooltip"
import MenuIcon from "@material-ui/icons/Menu"
import Toolbar from "@material-ui/core/Toolbar"
import Typography from "@material-ui/core/Typography"
import { makeStyles} from "@material-ui/core/styles"
import PathRecorder from "../path-recorder"
import { hasErrors } from "../shared/utilities"
import Networking from "../networking"

const useStyles = makeStyles((theme) => ({
  appBar: {
    [theme.breakpoints.up("md")]: {
      width: `calc(100% - ${DRAWER_WIDTH}px)`,
      marginLeft: DRAWER_WIDTH,
    },
  },
  title: {
    marginRight: "auto",
  },
  menuButton: {
    [theme.breakpoints.up("md")]: {
      display: "none",
    },
  },
  stop: {
    marginLeft: theme.spacing(1.5),
  },
  start: {
    backgroundColor: "green",
    color: "white",
    marginLeft: theme.spacing(1.5),
    "&:hover": {
      background: "darkgreen",
    },
  },
  connection: {
    color: "lightgray",
  },
  state: {
    marginLeft: theme.spacing(1.5),
    color: "lightgray",
  },
  errorIcon: {
    marginLeft: theme.spacing(1.5),
    color: "red",
  },
  reconnect: {
    marginLeft: theme.spacing(1.5),
    backgroundColor: "green",
    color: "white",
    "&:hover": {
      backgroundColor: "darkgreen",
    },
  },
  idle: {
    marginLeft: theme.spacing(1.5),
    backgroundColor: "green",
    color: "white",
    "&:hover": {
      backgroundColor: "darkgreen",
    },
  },
}))

interface Props {
  networking: Networking
  setMobileOpen: React.Dispatch<React.SetStateAction<boolean>>
  topBarTitle: string
  pathRecorder: PathRecorder
  handleRobotConnect: (id: string) => void
}

export default function Component({
  networking,
  setMobileOpen,
  topBarTitle,
  pathRecorder,
  handleRobotConnect,
}: Props) {
  const classes = useStyles()

  const guiState = networking.guiState
  const robotState = networking.robotState
  const senders = networking.senders

  function handleStopButton() {
    senders.stop()
  }

  function handleIdle() {
    senders.idle()
  }

  return (
    <AppBar position="fixed" className={classes.appBar}>
      <Toolbar>
        <IconButton
          color="inherit"
          aria-label="open drawer"
          edge="start"
          onClick={() => setMobileOpen(true)}
          className={classes.menuButton}
        >
          <MenuIcon />
        </IconButton>

        <Typography variant="h6" noWrap className={classes.title}>
          {topBarTitle}
        </Typography>

        {pathRecorder.isRecording ? (
          <Tooltip title="Recording">
            <FiberManualRecordIcon style={{ color: "red", marginRight: "6px" }} />
          </Tooltip>
        ) : null}

        {guiState.isConnected ? (
          <>
            <Hidden xsDown>
              <Typography variant="body2" className={classes.connection}>
                {`ID: ${guiState.id}`}
              </Typography>
            </Hidden>

            <Typography variant="body2" className={classes.state}>
              {OVERSEER_STATE_STRING[robotState.overseerState]}
            </Typography>
          </>
        ) : (
          <Typography variant="body2" className={classes.connection}>
            not connected
          </Typography>
        )}

        {!guiState.isConnected && networking.lastConnectedId !== "" && (
          <Button
            className={classes.reconnect}
            variant="contained"
            onClick={() => handleRobotConnect("useRecentId")}
          >
            reconnect
          </Button>
        )}

        {guiState.isConnected && hasErrors(robotState) ? (
          <Tooltip title="ERROR">
            <ErrorOutlineIcon fontSize="large" className={classes.errorIcon} />
          </Tooltip>
        ) : null}

        {guiState.isConnected &&
          (robotState.overseerState === 1 ||
            robotState.overseerState === 2 ||
            robotState.overseerState === 7 ||
            robotState.overseerState === 4) && ( // if in manual, auto, idle, or return_to_start
            <Button className={classes.stop} variant="contained" color="secondary" onClick={handleStopButton}>
              stop
            </Button>
          )}

        {guiState.isConnected &&
          (robotState.overseerState === 5 || robotState.overseerState === 6) && ( // if in stopped or decending
            <Button className={classes.idle} variant="contained" onClick={handleIdle}>
              idle
            </Button>
          )}
      </Toolbar>
    </AppBar>
  )
}
