import React, { useEffect } from "react"
import Grid from "@material-ui/core/Grid"

import GeneralCard from "../components/GeneralCard"
import GpsCard from "../components/GpsCard"
import MotorControllerCard from "../components/MotorControllerCard"
import SubjectVehicleCard from "../components/SubjectVehicleCard"
import Networking from "../networking"
import Box from "@material-ui/core/Box"
import Typography from "@material-ui/core/Typography"

interface Props {
  networking: Networking
  setTopBarTitle: React.Dispatch<React.SetStateAction<string>>
}

export default function Page({ networking, setTopBarTitle }: Props) {
  const robotState = networking.robotState

  useEffect(() => {
    setTopBarTitle("Robot Status")
  }, [setTopBarTitle])
  return (
    <div>
      <Grid container spacing={2} alignItems="flex-start">
        <Grid item>
          <GeneralCard robotState={robotState} />
        </Grid>

        <Grid item>
          <GpsCard robotState={robotState} />
        </Grid>

        <Grid item>
          <MotorControllerCard
            motorController={robotState.motorControllers.leftFront}
            id={1}
            location="left_front"
          />
        </Grid>

        <Grid item>
          <MotorControllerCard
            motorController={robotState.motorControllers.leftBack}
            id={2}
            location="left_back"
          />
        </Grid>

        <Grid item>
          <MotorControllerCard
            motorController={robotState.motorControllers.rightFront}
            id={3}
            location="right_front"
          />
        </Grid>

        <Grid item>
          <MotorControllerCard
            motorController={robotState.motorControllers.rightBack}
            id={4}
            location="right_back"
          />
        </Grid>

        <Grid item>
          <SubjectVehicleCard robotState={robotState} />
        </Grid>
      </Grid>

      <Typography variant="h6" style={{ marginTop: "15px" }}>
        Error Log
      </Typography>
      <Box
        mb={2}
        display="flex"
        flexDirection="column"
        height="250px"
        style={{
          border: "2px solid lightgrey",
          overflow: "hidden",
          overflowY: "scroll",
          overflowX: "scroll",
          padding: "10px",
        }}
      >
        {networking.errorMessages.map((msg) => (
          <div key={msg}> {msg} </div>
        ))}
      </Box>
    </div>
  )
}
