import React from "react"
import { DRAWER_WIDTH } from "../shared/constants"
import { useHistory } from "react-router-dom"

import Divider from "@material-ui/core/Divider"
import Drawer from "@material-ui/core/Drawer"
import Hidden from "@material-ui/core/Hidden"
import List from "@material-ui/core/List"
import ListItem from "@material-ui/core/ListItem"
import ListItemText from "@material-ui/core/ListItemText"
import Typography from "@material-ui/core/Typography"
import ErrorOutlineIcon from "@material-ui/icons/ErrorOutline"
import Tooltip from "@material-ui/core/Tooltip"
import { makeStyles, useTheme } from "@material-ui/core/styles"
import { hasErrors } from "../shared/utilities"
import Networking from "../networking"

const useStyles = makeStyles((theme) => ({
  toolbar: theme.mixins.toolbar, // necessary for pushing the SideDrawer below the TopBar
  drawerPaper: {
    width: DRAWER_WIDTH,
  },
  logo: {
    paddingTop: 10,
  },
  list: {
    padding: 0,
  },
  errorIcon: {
    color: "red",
  },
}))

interface Props {
  networking: Networking
  mobileOpen: boolean
  setMobileOpen: React.Dispatch<React.SetStateAction<boolean>>
}

export default function Component({ networking, mobileOpen, setMobileOpen }: Props) {
  const classes = useStyles()
  const theme = useTheme()
  const history = useHistory()

  const itemsLIst = [
    {
      text: "Initial Setup",
      onClick: () => {
        history.push("/initialsetup")
        setMobileOpen(false)
      },
      canShowErrorIcon: false,
    },
    {
      text: "Manual Control",
      onClick: () => {
        history.push("/manualcontrol")
        setMobileOpen(false)
      },
      canShowErrorIcon: false,
    },
    {
      text: "Robot Status",
      onClick: () => {
        history.push("/robotstatus")
        setMobileOpen(false)
      },
      canShowErrorIcon: true,
    },
    {
      text: "Path",
      onClick: () => {
        history.push("/path")
        setMobileOpen(false)
      },
      canShowErrorIcon: false,
    },
    {
      text: "Path Following",
      onClick: () => {
        history.push("/pathfollowing")
        setMobileOpen(false)
      },
      canShowErrorIcon: false,
    },
  ]

  const drawer = (
    <div>
      <div className={classes.toolbar}>
        <Typography variant="h4" align="center" color="primary" className={classes.logo}>
          STRIDE
        </Typography>
      </div>

      <Divider />
      <List className={classes.list}>
        {itemsLIst.map((item) => {
          const { text, onClick, canShowErrorIcon } = item
          return (
            <ListItem button key={text} onClick={onClick}>
              <ListItemText primary={text} />
              {canShowErrorIcon && hasErrors(networking.robotState) && networking.guiState.isConnected ? (
                <Tooltip title="ERROR">
                  <ErrorOutlineIcon className={classes.errorIcon} />
                </Tooltip>
              ) : null}
            </ListItem>
          )
        })}
      </List>
    </div>
  )

  return (
    <nav aria-label="mailbox folders">
      <Hidden mdUp implementation="css">
        <Drawer
          variant="temporary"
          anchor={theme.direction === "rtl" ? "right" : "left"}
          open={mobileOpen}
          onClose={() => setMobileOpen(false)}
          classes={{
            paper: classes.drawerPaper,
          }}
          ModalProps={{
            keepMounted: true, // Better open performance on mobile.
          }}
        >
          {drawer}
        </Drawer>
      </Hidden>

      <Hidden smDown implementation="css">
        <Drawer
          classes={{
            paper: classes.drawerPaper,
          }}
          variant="permanent"
          open
        >
          {drawer}
        </Drawer>
      </Hidden>
    </nav>
  )
}
