import { GuiState, RobotState, Senders } from "./shared/interfaces"

export default class Networking {
  guiState: GuiState
  robotState: RobotState
  socket: WebSocket
  senders: Senders
  dataArrivalTime: number
  intervalId1!: NodeJS.Timeout
  intervalId2!: NodeJS.Timeout
  receivedSharedPath: { timestamp: string; latitudes: Array<number>; longitudes: Array<number> }
  pathToFollow: { latitudes: Array<number>; longitudes: Array<number> }
  lastConnectedId: string
  isConnectedOnce: boolean
  isNewTestDataReady: boolean
  testLogMessages: Array<string>
  errorMessages: Array<string>

  constructor(id: string, isConnectedOnce: boolean) {
    if (isConnectedOnce) {
      this.lastConnectedId = id
      this.isConnectedOnce = true
    } else {
      this.lastConnectedId = ""
      this.isConnectedOnce = false
    }

    this.guiState = {
      isConnected: false,
      id: id,
    }

    // robotState:
    //   -should have the same keys as the received JSON message from robot
    //   -JSON message is received continuosly at certain rate
    this.robotState = {
      type: "robotState",
      robotVelocityCommand: {
        v: 0,
        w: 0,
      },
      robotTurningRadius: 0,
      overseerState: 0,
      websocketClientCount: 0,
      doesBrakeWhenStopped: false,
      robotTemperature: 0,
      batteryTemperature: 0,
      batteryVoltage: 0,
      tempErrorWord: 0,
      motorControllers: {
        leftFront: {
          state: 0,
          heartbeatNmt: 0,
          isHeartbeatTimeout: false,
          motorCurrentDraw: 0,
          wheelRpmActual: 0,
          errorWord: 0,
          windingTemperature: 0,
        },
        leftBack: {
          state: 0,
          heartbeatNmt: 0,
          isHeartbeatTimeout: false,
          motorCurrentDraw: 0,
          wheelRpmActual: 0,
          errorWord: 0,
          windingTemperature: 0,
        },
        rightFront: {
          state: 0,
          heartbeatNmt: 0,
          isHeartbeatTimeout: false,
          motorCurrentDraw: 0,
          wheelRpmActual: 0,
          errorWord: 0,
          windingTemperature: 0,
        },
        rightBack: {
          state: 0,
          heartbeatNmt: 0,
          isHeartbeatTimeout: false,
          motorCurrentDraw: 0,
          wheelRpmActual: 0,
          errorWord: 0,
          windingTemperature: 0,
        },
      },
      gps: {
        status: -1,
        latitude: 0,
        longitude: 0,
        northVelocity: 0,
        eastVelocity: 0,
        zAngularVelocity: 0,
        heading: 0,
      },
      pathFollower: {
        pathName: "",
        scriptName: "",
      },
      targetVehicle: {
        latitude: 0,
        longitude: 0,
        heading: 0,
        velocity: 0,
        gps_ready: false,
        gps_correction_type: 0,
      },
    }

    // Here are the attributes that don't get updated continuously
    this.receivedSharedPath = { timestamp: "", latitudes: [], longitudes: [] }
    this.pathToFollow = { latitudes: [], longitudes: [] }
    this.isNewTestDataReady = false
    this.testLogMessages = []
    this.errorMessages = []

    // For disconnecting when no more incoming data
    this.dataArrivalTime = 0

    try {
      if (id === "localhost") {
        // for debug
        this.socket = new WebSocket("ws://localhost:9000")
      } else if (id === "999") {
        // for debug
        this.socket = new WebSocket("ws://192.168.1.129:9000")
      } else {
        this.socket = new WebSocket("ws://195.0.0." + id + ":9000")
      }
    } catch (error) {
      console.error(error)
      this.socket = new WebSocket("ws://0.0.0.0")
    }

    this.socket.onopen = (event) => {
      this.guiState.isConnected = true
      this.dataArrivalTime = Date.now()

      this.lastConnectedId = this.guiState.id
      this.isConnectedOnce = true

      // disconnect if no messages are received for a period of time
      this.intervalId1 = setInterval(() => {
        if (Date.now() - this.dataArrivalTime > 4000) {
          this.guiState.isConnected = false // needed when wifi gets disconnected
          this.socket.close()
        }
      }, 250)

      // send out heartbeat
      this.intervalId2 = setInterval(() => {
        this.senders.heartbeat()
      }, 200)
    }

    this.socket.onmessage = (event) => {
      this.dataArrivalTime = Date.now()

      const data = JSON.parse(event.data)

      if (data.type === "robotState") {
        this.robotState = data
      } else if (data.type === "sharedPath") {
        const d = new Date()
        const timestamp = d.toLocaleTimeString()

        this.receivedSharedPath = {
          timestamp: timestamp,
          latitudes: data.latitudes,
          longitudes: data.longitudes,
        }
      } else if (data.type === "/path_follower/path_to_follow") {
        this.pathToFollow.latitudes = data.latitudes
        this.pathToFollow.longitudes = data.longitudes
      } else if (data.type === "createdCSV") {
        this.isNewTestDataReady = true
      } else if (data.type === "commandMessage") {
        this.testLogMessages.push(data.commandMessage)
      } else if (data.type === "errorMessage") {
        // Attach a date to the error message
        const d = new Date()
        const timestamp = d.toLocaleTimeString()
        this.errorMessages.push(timestamp)

        // Attach the error messages to both test log and error log
        const newline_separated = data.errorMessage.split(/\r?\n/)
        for (const error_message of newline_separated) {
          // "2" is Auto
          if (this.robotState.overseerState === 2) {
            this.testLogMessages.push(error_message)
          }
          this.errorMessages.push(error_message)
        }
      }
    }

    // the onclose callback will not run immediately when wifi gets disconnected
    this.socket.onclose = (event) => {
      this.guiState.isConnected = false
      clearInterval(this.intervalId1)
      clearInterval(this.intervalId2)
    }

    this.senders = {
      joystick: (travel, angle) => {
        const message = {
          type: "/joystick",
          travel: travel,
          angle: angle,
        }

        if (this.guiState.isConnected) {
          this.socket.send(JSON.stringify(message))
        }
      },

      spin: (angular_velocity) => {
        const message = {
          type: "/robot_velocity_command",
          x: 0,
          theta: angular_velocity,
        }

        // overseerState "1" is MANUAL state
        if (this.guiState.isConnected && this.robotState.overseerState === 1) {
          this.socket.send(JSON.stringify(message))
        }
      },

      moveForward: (velocity) => {
        const message = {
          type: "/robot_velocity_command",
          x: velocity,
          theta: 0,
        }
        // overseerState "1" is MANUAL state
        if (this.guiState.isConnected && this.robotState.overseerState === 1) {
          this.socket.send(JSON.stringify(message))
        }
      },

      stop: () => {
        const message = {
          type: "/gui/stop_clicked",
        }
        if (this.guiState.isConnected) {
          this.socket.send(JSON.stringify(message))
        }
      },

      enableManaul: () => {
        if (this.guiState.isConnected) {
          this.socket.send(JSON.stringify({ type: "/gui/enable_manual_clicked" }))
        }
      },

      idle: () => {
        if (this.guiState.isConnected) {
          this.socket.send(JSON.stringify({ type: "/gui/idle_clicked" }))
        }
      },

      heartbeat: () => {
        if (this.socket.readyState === WebSocket.OPEN) {
          this.socket.send(JSON.stringify({ type: "/gui/heartbeat" }))
        }
      },

      toggleBrakeWhenStopped: () => {
        if (this.guiState.isConnected) {
          this.socket.send(JSON.stringify({ type: "/gui/brake_when_stopped_toggled" }))
        }
      },

      setMagneticCalibration: (method) => {
        const message = {
          type: "/an_device/magnetic_calibration/calibrate",
          method: method,
        }
        if (this.guiState.isConnected) {
          this.socket.send(JSON.stringify(message))
        }
      },

      sharePath: (latitudes, longitudes) => {
        const message = {
          type: "sharedPath",
          latitudes: latitudes,
          longitudes: longitudes,
        }
        if (this.guiState.isConnected) {
          this.socket.send(JSON.stringify(message))
        }
      },

      startPathFollowing: () => {
        if (this.guiState.isConnected) {
          this.socket.send(JSON.stringify({ type: "/gui/start_path_following_clicked" }))
        }
      },

      uploadPath: (filename, fileContent) => {
        // return if file extension is not txt
        if (filename.split(".")[1] !== "txt") {
          return
        }

        const message = {
          type: "/gui/upload_path_clicked",
          filename: filename,
          fileContent: fileContent,
        }
        this.socket.send(JSON.stringify(message))
      },

      uploadScript: (filename, fileContent) => {
        // return if file extension is not py
        if (filename.split(".")[1] !== "py") {
          return
        }

        const message = {
          type: "/gui/upload_script_clicked",
          filename: filename,
          fileContent: fileContent,
        }
        this.socket.send(JSON.stringify(message))
      },

      returnToStart: () => {
        if (this.guiState.isConnected) {
          this.socket.send(JSON.stringify({ type: "/gui/return_to_start_clicked" }))
        }
      },
    }
  }
}
