// import React from "react"

import { makeStyles } from "@material-ui/core/styles"
import Card from "@material-ui/core/Card"
import CardContent from "@material-ui/core/CardContent"
import Typography from "@material-ui/core/Typography"
import { RobotState } from "../shared/interfaces"

const useStyles = makeStyles((theme) => ({
  title: {
    margin: theme.spacing(1),
  },
  body: {
    margin: theme.spacing(1),
  },
  expand: {
    margin: "0 auto",
    paddingTop: 0,
    paddingBottom: 0,
    transform: "rotate(0deg)",
    marginLeft: "auto",
    transition: theme.transitions.create("transform", {
      duration: theme.transitions.duration.shortest,
    }),
  },
  expandOpen: {
    transform: "rotate(180deg)",
  },
  cardContent: {
    paddingBottom: 0,
  },
  copyButton: {
    paddingTop: 0,
    marginLeft: theme.spacing(0.5),
  },
}))

export default function Component({ robotState }: { robotState: RobotState }) {
  const classes = useStyles()

  return (
    <Card variant="outlined">
      <CardContent className={classes.cardContent}>
        <Typography variant="h5" className={classes.title}>
          Subject Vehicle
        </Typography>

        <Typography variant="body1" className={classes.body}>
          Latitude (deg): {robotState.targetVehicle.latitude.toFixed(7)}
        </Typography>

        <Typography variant="body1" className={classes.body}>
          Longitude (deg): {robotState.targetVehicle.longitude.toFixed(7)}
        </Typography>

        <Typography variant="body1" className={classes.body}>
          Heading (deg): {robotState.targetVehicle.heading.toFixed(2)}
        </Typography>

        <Typography variant="body1" className={classes.body}>
          Velocity (m/s): {robotState.targetVehicle.velocity.toFixed(2)}
        </Typography>

        <Typography variant="body1" className={classes.body}>
          GPS Ready: {robotState.targetVehicle.gps_ready.toString()}
        </Typography>

        <Typography variant="body1" className={classes.body}>
          GPS Correction Type: {robotState.targetVehicle.gps_correction_type}
        </Typography>
      </CardContent>
    </Card>
  )
}
