// abbreviations
// lat: latitude
// long: longitude
// ref: reference
// pos: position

import { RobotState } from "./interfaces"

export function hasErrors(robotState: RobotState) {
  return (
    (robotState.motorControllers.leftFront.errorWord !== 0 &&
      robotState.motorControllers.leftFront.errorWord !== 64) ||
    robotState.motorControllers.leftFront.isHeartbeatTimeout ||
    (robotState.motorControllers.leftBack.errorWord !== 0 &&
      robotState.motorControllers.leftBack.errorWord !== 64) ||
    robotState.motorControllers.leftBack.isHeartbeatTimeout ||
    (robotState.motorControllers.rightFront.errorWord !== 0 &&
      robotState.motorControllers.rightFront.errorWord !== 64) ||
    robotState.motorControllers.rightFront.isHeartbeatTimeout ||
    (robotState.motorControllers.rightBack.errorWord !== 0 &&
      robotState.motorControllers.rightBack.errorWord !== 64) ||
    robotState.motorControllers.rightBack.isHeartbeatTimeout ||
    robotState.tempErrorWord !== 0
  )
}

export function EN_factors(lat_ref: number) {
  const e = 0.0818191908426
  const R = 6378137
  const pi = Math.PI

  const sin = Math.sin
  const cos = Math.cos
  const sqrt = Math.sqrt

  const E_factor =
    (((cos((lat_ref * pi) / 180) * R) / sqrt(1 - sin((lat_ref * pi) / 180) ** 2 * e ** 2)) * pi) / 180

  const N_factor =
    ((((1 - e ** 2) * R) /
      ((1 - sin((lat_ref * pi) / 180) ** 2 * e ** 2) * sqrt(1 - sin((lat_ref * pi) / 180) ** 2 * e ** 2))) *
      pi) /
    180
  return [E_factor, N_factor]
}

// Latitude Longitude to North East
export function LL2NE(
  latitude: number,
  longitude: number,
  lat_ref: number,
  long_ref: number,
  E_factor: number,
  N_factor: number
) {
  const pos_east = (longitude - long_ref) * E_factor
  const pos_north = (latitude - lat_ref) * N_factor
  return [pos_north, pos_east]
}

export function NE2LL(
  pos_north: number,
  pos_east: number,
  lat_ref: number,
  long_ref: number,
  E_factor: number,
  N_factor: number
) {
  const longitude = pos_east / E_factor + long_ref
  const latitude = pos_north / N_factor + lat_ref
  return [latitude, longitude]
}
