import React, { useEffect, useState } from "react"

import { makeStyles } from "@material-ui/core/styles"

import Snackbar from "@material-ui/core/Snackbar"
import MuiAlert from "@material-ui/lab/Alert"

import Networking from "../networking"

const useStyles = makeStyles((theme) => ({
  snackbar: {
    opacity: 0.85,
  },
}))

interface Props {
  networking: Networking
}

export default function Component({ networking }: Props) {
  const classes = useStyles()

  const [snackbarOpen, setSnackbarOpen] = useState(false)

  useEffect(() => {
    if (networking.isNewTestDataReady === true) {
      setSnackbarOpen(true)
      networking.isNewTestDataReady = false
    }
  }, [setSnackbarOpen, networking, networking.isNewTestDataReady])

  return (
    <div>
      <Snackbar
        className={classes.snackbar}
        open={snackbarOpen}
        autoHideDuration={5000}
        onClose={() => {
          setSnackbarOpen(false)
        }}
        anchorOrigin={{ vertical: "top", horizontal: "center" }}
      >
        <MuiAlert elevation={6} variant="filled" severity="info">
          Test Data Ready for Download
        </MuiAlert>
      </Snackbar>
    </div>
  )
}
