import { makeStyles } from "@material-ui/core/styles"
import Button from "@material-ui/core/Button"
import Typography from "@material-ui/core/Typography"
import Networking from "../../networking"
import { saveAs } from "file-saver"

const useStyles = makeStyles((theme) => ({
  notes: {
    margin: theme.spacing(1),
    marginBottom: theme.spacing(3),
  },
  body: {
    margin: theme.spacing(1),
  },
  button: {
    margin: theme.spacing(1),
  },
}))

interface Props {
  networking: Networking
}

export default function Component({ networking }: Props) {
  const classes = useStyles()

  const sharedPath = networking.receivedSharedPath

  function exportPath() {
    let pathString = "Global\n"
    for (let i = 0; i < sharedPath.latitudes.length; i++) {
      pathString = pathString.concat(
        sharedPath.latitudes[i].toFixed(17).toString(),
        " ",
        sharedPath.longitudes[i].toFixed(17).toString(),
        "\n"
      )
    }
    let blob = new Blob([pathString], { type: "text/plain;charset=utf-8" })
    saveAs(blob, "stride_path.txt")
  }

  return (
    <div>
      <Typography variant="body1" className={classes.notes}>
        Shared path from another device will appear here.
      </Typography>

      <Typography variant="body1" className={classes.body}>
        Time Received: {sharedPath.timestamp} <br />
        Number of Points: {sharedPath.latitudes.length}
      </Typography>

      <Button
        className={classes.button}
        variant="outlined"
        color="primary"
        onClick={() => {
          exportPath()
        }}
      >
        export To file
      </Button>
    </div>
  )
}
