import React, { useState } from "react"
import { makeStyles } from "@material-ui/core/styles"
import Button from "@material-ui/core/Button"
import Snackbar from "@material-ui/core/Snackbar"
import MuiAlert from "@material-ui/lab/Alert"
import Divider from "@material-ui/core/Divider"
import Typography from "@material-ui/core/Typography"
import PathRecorder from "../../path-recorder"
import Networking from "../../networking"
import { saveAs } from "file-saver"

const useStyles = makeStyles((theme) => ({
  notes: {
    margin: theme.spacing(1),
    marginBottom: theme.spacing(3),
  },
  body: {
    margin: theme.spacing(1),
  },
  button: {
    margin: theme.spacing(1),
  },
}))

interface Props {
  networking: Networking
  pathRecorder: PathRecorder
}

export default function Component({ networking, pathRecorder }: Props) {
  const classes = useStyles()
  const [snackOpen, setSnackOpen] = useState(false)

  function exportPath() {
    let pathString = "Global\n"
    for (let i = 0; i < pathRecorder.numberOfPoints; i++) {
      pathString = pathString.concat(
        pathRecorder.latitudes[i].toFixed(17).toString(),
        " ",
        pathRecorder.longitudes[i].toFixed(17).toString(),
        "\n"
      )
    }
    let blob = new Blob([pathString], { type: "text/plain;charset=utf-8" })
    saveAs(blob, "stride_path.txt")
  }

  return (
    <div>
      <Typography variant="body1" className={classes.notes}>
        <ul style={{ margin: 0, paddingLeft: 20 }}>
          <li>The GPS cordinates of the robot can be recorded here.</li>
          <li>Move the robot to create a path when recording starts.</li>
          <li>Tips for recording:</li>
          <ul>
            <li>Since speed is not recorded, move robot slowly to accurately capture path.</li>
            <li>
              You may record path that has sharp corners. Robot can be programmed later to stop at the corner
              and rotate to the specified heading before moving again.
            </li>
          </ul>
        </ul>
      </Typography>
      <Divider />

      <Typography variant="body1" className={classes.body}>
        Status: {pathRecorder.isRecording ? "Recording..." : "Not Recording"}
      </Typography>

      <Typography variant="body1" className={classes.body}>
        Points Recorded: {pathRecorder.numberOfPoints}
      </Typography>

      <Button
        className={classes.button}
        variant="outlined"
        color="primary"
        onClick={() => {
          pathRecorder.requestRecording()
        }}
      >
        record
      </Button>

      <Button
        className={classes.button}
        variant="outlined"
        color="primary"
        onClick={() => {
          pathRecorder.stopRecording()
        }}
      >
        stop
      </Button>

      <br />

      <Button
        className={classes.button}
        variant="outlined"
        color="primary"
        onClick={() => {
          exportPath()
        }}
      >
        export To file
      </Button>

      <br />

      <Snackbar
        open={snackOpen}
        autoHideDuration={8000}
        onClose={() => {
          setSnackOpen(false)
        }}
      >
        <MuiAlert elevation={6} variant="filled" severity="info">
          Go to the SHARED tab above in another connected device.
        </MuiAlert>
      </Snackbar>

      <Button
        className={classes.button}
        variant="outlined"
        color="primary"
        onClick={() => {
          setSnackOpen(true)
          networking.senders.sharePath(pathRecorder.latitudes, pathRecorder.longitudes)
        }}
      >
        share with other devices
      </Button>
    </div>
  )
}
