import React, { Component } from "react"

import ReactNipple from "react-nipple"
import "react-nipple/lib/styles.css"
import Tooltip from "@material-ui/core/Tooltip"
import PathRecorderPopUpCard from "./PathRecorderPopUpCard"
import PathRecorder from "../path-recorder"
import Networking from "../networking"

// Abbreviations:
// ICR = instantaneous center of rotation
// v,V = linear velocity
// w,W = angular velocity
// r,R = turning radius of robot geometric center

interface Props {
  networking: Networking
  pathRecorder: PathRecorder
}

export default class Joystick extends Component<Props> {
  circleDiameter = 400 // pixels
  sender_thottle_time = 100 //ms
  previousTime = Date.now()

  render() {
    return (
      <div>
        <h3 style={{ marginTop: 0, marginBottom: 5 }}>Press and drag below to move the robot</h3>

        <Tooltip title="Linear Velocity">
          <span style={{ fontSize: 16 }}>
            v: {this.props.networking.robotState.robotVelocityCommand.v.toFixed(2)} m/s
          </span>
        </Tooltip>

        <Tooltip title="Angular Velocity">
          <span style={{ fontSize: 16, marginLeft: 16}}>
            w: {((this.props.networking.robotState.robotVelocityCommand.w / Math.PI) * 180).toFixed(0)} deg/s
          </span>
        </Tooltip>

        <Tooltip title="Heading">
          <span style={{ fontSize: 16, marginLeft: 16 }}>
            h: {((this.props.networking.robotState.gps.heading / Math.PI) * 180).toFixed(0)} deg
          </span>
        </Tooltip>

        <br />
        {this.props.pathRecorder.isRecording ? (
          <PathRecorderPopUpCard pathRecorder={this.props.pathRecorder} networking={this.props.networking} />
        ) : null}

        <ReactNipple
          className="nipple"
          options={{
            mode: "dynamic",
            color: "blue",
            position: { top: "50%", left: "50%" },
            size: this.circleDiameter,
            nippleSize: 90,
          }}
          style={{
            zIndex: 0,
            outline: `1px dashed blue`,
            width: "100%",
            height: "max( 100vh - 325px, 120px)", // calc() not needed inside max()
            // display: "table",
            overflow: "hidden",
          }}
          onStart={this.handleJoystickStart}
          onEnd={this.handleJoystickEnd}
          onMove={this.handleJoystickMove}
        />
        {/* <DebugView data={this.state.data} /> */}
      </div>
    )
  }

  handleJoystickStart = (evt: any, data: any) => {}

  handleJoystickEnd = (evt: any, data: any) => {
    setTimeout(() => {
      this.props.networking.senders.joystick(0, 0)
    }, 100) // needed, otherwise the subscriber callback for /robot_velocity_command won't get triggered
  }
  handleJoystickMove = (evt: any, data: any) => {
    if (Date.now() - this.previousTime < this.sender_thottle_time) {
      return
    }
    this.previousTime = Date.now()

    const travel = data.distance / (this.circleDiameter * 0.5)
    const angle = data.angle.radian - Math.PI / 2 // the angle when pushing the joystick forwards is now 0

    this.props.networking.senders.joystick(travel, angle)
  }
}
