// abbreviations:
// refLat: reference latitude
// refLong: reference longitude
// EN: East North

import { EN_factors, LL2NE, NE2LL } from "./shared/utilities"

export default class PathRecorder {
  isRecording: boolean
  isRecordingLine: boolean
  hasBeenRecordingLine: boolean
  pointDistance: number
  latitudes: number[]
  longitudes: number[]
  priorLatitude: number
  priorLongitude: number
  latRef: number
  longRef: number
  E_factor: number
  N_factor: number
  recorderId: number
  numberOfPoints: number

  constructor() {
    this.isRecording = false
    this.isRecordingLine = false
    this.hasBeenRecordingLine = false
    this.pointDistance = 0.3 // meters
    this.latitudes = []
    this.longitudes = []
    this.priorLatitude = -1
    this.priorLongitude = -1
    this.latRef = -1
    this.longRef = -1
    this.E_factor = 0
    this.N_factor = 0
    this.recorderId = 0
    this.numberOfPoints = 0
  }

  requestRecording() {
    this.isRecording = true
    this.isRecordingLine = false
    this.hasBeenRecordingLine = false
    this.latitudes = []
    this.longitudes = []
    this.priorLatitude = -1
    this.priorLongitude = -1
    this.numberOfPoints = 0
  }

  stopRecording() {
    this.isRecording = false
    this.isRecordingLine = false

    //DO NOT ADD THIS HERE: this.hasBeenRecordingLine = false
  }

  toggleLineRecording() {
    this.isRecordingLine = !this.isRecordingLine
  }

  recordWhenRequested(latitude: number, longitude: number) {
    if (this.isRecording) {
      const haversineDistance = this.haversine(this.priorLatitude, latitude, this.priorLongitude, longitude)

      if (this.priorLatitude === -1 || this.priorLongitude === -1) {
        this.priorLatitude = latitude
        this.priorLongitude = longitude

        this.latitudes.push(latitude)
        this.longitudes.push(longitude)
        this.numberOfPoints += 1

        this.latRef = latitude
        this.longRef = longitude

        const EN = EN_factors(this.latRef)
        this.E_factor = EN[0]
        this.N_factor = EN[1]
      } else if (this.isRecordingLine) {
        this.hasBeenRecordingLine = true
      } else if (this.hasBeenRecordingLine) {
        this.hasBeenRecordingLine = false
        const [northStart, eastStart] = LL2NE(
          this.priorLatitude,
          this.priorLongitude,
          this.latRef,
          this.longRef,
          this.E_factor,
          this.N_factor
        )
        const [northEnd, eastEnd] = LL2NE(
          latitude,
          longitude,
          this.latRef,
          this.longRef,
          this.E_factor,
          this.N_factor
        )
        const lineDistance = ((northStart - northEnd) ** 2 + (eastStart - eastEnd) ** 2) ** 0.5
        const divisions = Math.floor(lineDistance / this.pointDistance)

        // good that the FOR loop won't run if divisions is < 1
        for (let i = 1; i <= divisions; i++) {
          const newNorth = northStart + (i / divisions) * (northEnd - northStart)
          const newEast = eastStart + (i / divisions) * (eastEnd - eastStart)
          const [newLatitude, newLongitude] = NE2LL(
            newNorth,
            newEast,
            this.latRef,
            this.longRef,
            this.E_factor,
            this.N_factor
          )
          this.latitudes.push(newLatitude)
          this.longitudes.push(newLongitude)
          this.numberOfPoints += 1

          this.priorLatitude = newLatitude
          this.priorLongitude = newLongitude
        }
      } else if (haversineDistance > this.pointDistance) {
        this.latitudes.push(latitude)
        this.longitudes.push(longitude)
        this.numberOfPoints += 1

        this.priorLatitude = latitude
        this.priorLongitude = longitude
      }
    }
  }

  // transformToRobotCenter(latRadian2: number, longRadian2: number, headingRadian: number) {
  //   const R = 6371008 // Earth mean radisu in meters
  //   const r = R * Math.cos(latRadian2) // radius of the earth circular slice at latitude2 in meters
  //   const d = 0.2736 // distance between gps and robot center in meters
  //   const theta = 0.11 // the radian angle between the gps and the robot center

  //   const latRadian1 = (R * latRadian2 - d * Math.cos(headingRadian + theta)) / R
  //   const longRadian1 = (r * longRadian2 - d * Math.sin(headingRadian + theta)) / r

  //   const latCentered = (latRadian1 / Math.PI) * 180
  //   const longCentered = (longRadian1 / Math.PI) * 180

  //   return [latCentered, longCentered]
  // }

  // Haversine Distance in meters between two latlong points
  haversine(lat1: number, lat2: number, long1: number, long2: number) {
    const latRadian1 = (lat1 / 180) * Math.PI
    const latRadian2 = (lat2 / 180) * Math.PI
    const longRadian1 = (long1 / 180) * Math.PI
    const longRadian2 = (long2 / 180) * Math.PI

    const R = 6371008 // Earth mean radisu in meters
    const deltaLat = latRadian2 - latRadian1
    const deltaLong = longRadian2 - longRadian1

    // Small angle approximation is used
    const distance =
      R * Math.sqrt(deltaLat ** 2 + Math.cos(latRadian1) * Math.cos(latRadian2) * deltaLong ** 2)

    return distance // in meters
  }
}
