import React, { useEffect } from "react"
import { makeStyles, Theme } from "@material-ui/core/styles"
import Tabs from "@material-ui/core/Tabs"
import Tab from "@material-ui/core/Tab"
import Box from "@material-ui/core/Box"
import Typography from "@material-ui/core/Typography"
import AppBar from "@material-ui/core/AppBar"

import PathRecorder from "../path-recorder"
import Networking from "../networking"
import PrepareTab from "../components/path_following/PrepareTab"
import TestTab from "../components/path_following/TestTab"

interface Props {
  networking: Networking
  setTopBarTitle: React.Dispatch<React.SetStateAction<string>>
  pathRecorder: PathRecorder
}

interface TabPanelProps {
  children?: React.ReactNode
  index: any
  value: any
}

function TabPanel(props: TabPanelProps) {
  const { children, value, index, ...other } = props

  return (
    <div role="tabpanel" hidden={value !== index} id={`simple-tabpanel-${index}`} {...other}>
      {value === index && (
        <Box p={3}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  )
}

const useStyles = makeStyles((theme: Theme) => ({
  root: {
    // flexGrow: 1,
    backgroundColor: theme.palette.background.paper,
  },
  tab: {
    color: "rgb(64,64,64)",
  },
}))

export default function Page({ networking, setTopBarTitle, pathRecorder }: Props) {
  const classes = useStyles()
  const [value, setValue] = React.useState(0)

  const handleChange = (event: React.ChangeEvent<{}>, newValue: number) => {
    setValue(newValue)
  }

  useEffect(() => {
    setTopBarTitle("Path Following")
  }, [setTopBarTitle])

  return (
    // <Grid container spacing={2} alignItems="flex-start">
    //   <Grid item>
    //     <PathRecorderCard robotState={robotState} pathRecorder={pathRecorder} />
    //   </Grid>
    // </Grid>
    <div className={classes.root}>
      <AppBar position="static" color="default">
        <Tabs
          value={value}
          variant="scrollable"
          scrollButtons="auto"
          indicatorColor="primary"
          textColor="primary"
          onChange={handleChange}
        >
          <Tab label="prepare" className={classes.tab} />
          <Tab label="test" className={classes.tab} />
        </Tabs>
      </AppBar>
      <TabPanel value={value} index={0}>
        <PrepareTab networking={networking} />
      </TabPanel>
      <TabPanel value={value} index={1}>
        <TestTab networking={networking} />
      </TabPanel>
    </div>
  )
}
