import React, { useState, useRef } from "react"

import { makeStyles } from "@material-ui/core/styles"
import Button from "@material-ui/core/Button"
import Typography from "@material-ui/core/Typography"
import Snackbar from "@material-ui/core/Snackbar"
import MuiAlert from "@material-ui/lab/Alert"

import Networking from "../../networking"

const useStyles = makeStyles((theme) => ({
  notes: {
    margin: theme.spacing(1),
    marginBottom: theme.spacing(3),
  },
  body: {
    margin: theme.spacing(1),
  },
  button: {
    margin: theme.spacing(1),
  },
  snackbar: {
    opacity: 0.9,
  },
}))

interface Props {
  networking: Networking
}

export default function Component({ networking }: Props) {
  const classes = useStyles()

  const uploadPathRef: React.MutableRefObject<any> = useRef()
  const uploadScriptRef: React.MutableRefObject<any> = useRef()

  const [wrongExtensionSnackbarOpen, setWrongExtensionSnackbarOpen] = useState(false)

  function uploadPath() {
    const file = uploadPathRef.current.files[0]
    uploadPathRef.current.value = "" // this is needed for uploading file with same filename but different content

    if (file.name.split(".")[1] !== "txt") {
      setWrongExtensionSnackbarOpen(true)
      return
    }

    const reader = new FileReader()
    reader.addEventListener("load", (event: any) => {
      const fileContent = event.target.result
      networking.senders.uploadPath(file.name, fileContent)
    })
    reader.readAsText(file)
  }

  function uploadScript() {
    const file = uploadScriptRef.current.files[0]
    uploadScriptRef.current.value = "" // this is needed for uploading file with same filename but different content

    if (file.name.split(".")[1] !== "py") {
      setWrongExtensionSnackbarOpen(true)
      return
    }

    const reader = new FileReader()
    reader.addEventListener("load", (event: any) => {
      const fileContent = event.target.result
      networking.senders.uploadScript(file.name, fileContent)
    })
    reader.readAsText(file)
  }

  return (
    <div>
      <Typography variant="body1" className={classes.notes}>
        Note that the robot only stores the most recently uploaded path and script files.
      </Typography>

      <Typography variant="body1" className={classes.body}>
        Path (.txt file): {networking.robotState.pathFollower.pathName} <br />
        Script (.py file): {networking.robotState.pathFollower.scriptName}
      </Typography>

      <Button className={classes.button} variant="outlined" color="primary" component="label">
        upload path
        <input
          type="file"
          ref={uploadPathRef}
          onChange={() => {
            uploadPath()
          }}
          hidden
        />
      </Button>

      <Button className={classes.button} variant="outlined" color="primary" component="label">
        upload script
        <input
          type="file"
          ref={uploadScriptRef}
          onChange={() => {
            uploadScript()
          }}
          hidden
        />
      </Button>

      <Snackbar
        className={classes.snackbar}
        open={wrongExtensionSnackbarOpen}
        autoHideDuration={6000}
        onClose={() => {
          setWrongExtensionSnackbarOpen(false)
        }}
        anchorOrigin={{ vertical: "top", horizontal: "center" }}
      >
        <MuiAlert elevation={6} variant="filled" severity="warning">
          Incorrect file extension
        </MuiAlert>
      </Snackbar>
    </div>
  )
}
