import React, { useState } from "react"

import { makeStyles } from "@material-ui/core/styles"
import Button from "@material-ui/core/Button"
import Card from "@material-ui/core/Card"
import CardContent from "@material-ui/core/CardContent"
import Typography from "@material-ui/core/Typography"
import TextField from "@material-ui/core/TextField"
import Networking from "../networking"

const useStyles = makeStyles((theme) => ({
  title: {
    margin: theme.spacing(1),
  },
  status: {
    margin: theme.spacing(1),
  },
  textField: {
    width: "15.5rem",
    margin: theme.spacing(1),
  },
  button: {
    margin: theme.spacing(1),
  },
}))

interface Props {
  networking: Networking
  handleRobotConnect: (id: string) => void
  handleRobotDisconnect: () => void
}

export default function Component({ networking, handleRobotConnect, handleRobotDisconnect }: Props) {
  const classes = useStyles()

  const [id, setId] = useState("")

  return (
    <Card variant="outlined">
      <CardContent>
        <Typography variant="h5" className={classes.title}>
          Robot Connection
        </Typography>

        <Typography variant="caption" display="block" className={classes.status}>
          {networking.guiState.isConnected ? `connected (ID: ${networking.guiState.id})` : "not connected"}
        </Typography>

        <TextField
          label="Robot ID"
          variant="outlined"
          className={classes.textField}
          onChange={(e) => {
            setId(e.target.value)
          }}
          onKeyDown={(e) => {
            if (e.key === "Enter") {
              handleRobotConnect(id)
            }
          }}
        />
        <br />

        <Button
          className={classes.button}
          variant="contained"
          color="primary"
          onClick={() => handleRobotConnect(id)}
        >
          Connect
        </Button>

        <Button
          className={classes.button}
          variant="outlined"
          color="primary"
          onClick={() => handleRobotDisconnect()}
        >
          Disconnect
        </Button>
      </CardContent>
    </Card>
  )
}
