import React, { useEffect } from "react"
import RobotConnectCard from "../components/RobotConnectCard"
import Grid from "@material-ui/core/Grid"
import Networking from "../networking"

interface Props {
  networking: Networking
  handleRobotConnect: (id: string) => void
  handleRobotDisconnect: () => void
  setTopBarTitle: React.Dispatch<React.SetStateAction<string>>
}

export default function Page({
  networking,
  handleRobotConnect,
  handleRobotDisconnect,
  setTopBarTitle,
}: Props) {
  useEffect(() => {
    setTopBarTitle("Initial Setup")
  }, [setTopBarTitle])

  return (
    <Grid container spacing={2} justify="center" alignItems="flex-start">
      <Grid item>
        <RobotConnectCard
          networking={networking}
          handleRobotConnect={handleRobotConnect}
          handleRobotDisconnect={handleRobotDisconnect}
        />
      </Grid>
    </Grid>
  )
}

