import { makeStyles } from "@material-ui/core/styles"
import Card from "@material-ui/core/Card"
import CardContent from "@material-ui/core/CardContent"
import Typography from "@material-ui/core/Typography"
import { RobotState } from "../shared/interfaces"
import ErrorOutlineIcon from "@material-ui/icons/ErrorOutline"

const useStyles = makeStyles((theme) => ({
  title: {
    margin: theme.spacing(1),
  },
  body: {
    margin: theme.spacing(1),
  },
  expand: {
    margin: "0 auto",
    paddingTop: 0,
    paddingBottom: 0,
    transform: "rotate(0deg)",
    marginLeft: "auto",
    transition: theme.transitions.create("transform", {
      duration: theme.transitions.duration.shortest,
    }),
  },
  expandOpen: {
    transform: "rotate(180deg)",
  },
  cardContent: {
    paddingBottom: 0,
  },
  errorIcon: {
    // marginLeft: theme.spacing(1.5),
    verticalAlign: "-3px",
    color: "red",
    fontSize: "17px",
  },
}))

export default function Component({ robotState }: { robotState: RobotState }) {
  const classes = useStyles()

  return (
    <Card variant="outlined">
      <CardContent className={classes.cardContent}>
        <Typography variant="h5" className={classes.title}>
          General
        </Typography>

        <Typography variant="body1" className={classes.body}>
          Battery Voltage: {robotState.batteryVoltage.toFixed(2)}V
        </Typography>

        <Typography variant="body1" className={classes.body}>
          Battery Temperature: {robotState.batteryTemperature}F{" "}
          {robotState.batteryTemperature >= 140 ? <ErrorOutlineIcon className={classes.errorIcon} /> : null}
        </Typography>

        <Typography variant="body1" className={classes.body}>
          Robot Internal Temperature: {robotState.robotTemperature}F{" "}
          {robotState.robotTemperature >= 140 ? <ErrorOutlineIcon className={classes.errorIcon} /> : null}
        </Typography>

        <Typography variant="body1" className={classes.body}>
          Clients Connected to Robot: {robotState.websocketClientCount}
        </Typography>

      </CardContent>
    </Card>
  )
}
