import "./App.css"
import React, { useState, useEffect } from "react"
import { HashRouter as Router, Switch, Route, Redirect } from "react-router-dom"
import { DRAWER_WIDTH } from "./shared/constants"

import Networking from "./networking"

import SideDrawer from "./components/SideDrawer"
import TopBar from "./components/TopBar"
import InitialSetup from "./pages/InitialSetup"
import ManualControl from "./pages/ManualControl"
import RobotStatus from "./pages/RobotStatus"
import Path from "./pages/Path"
import PathFollowing from "./pages/PathFollowing"
import AutoDownhill from "./components/AutoDownhill"
import TestDataReadyPopUp from "./components/TestDataReadyPopUp"

import CssBaseline from "@material-ui/core/CssBaseline"
import { makeStyles } from "@material-ui/core/styles"
import PathRecorder from "./path-recorder"

let networking = new Networking("", false)

const pathRecorder = new PathRecorder() // initialized in App.tsx because components like TopBar needs its data

const useStyles = makeStyles((theme) => ({
  toolbar: theme.mixins.toolbar, // necessary for content to be below app bar
  content: {
    [theme.breakpoints.up("md")]: {
      width: `calc(100% - ${DRAWER_WIDTH}px)`,
      marginLeft: DRAWER_WIDTH,
    },
    padding: theme.spacing(3),
  },
}))

export default function App() {
  const classes = useStyles()

  const [forceRerender, setForceRerender] = useState({})
  const [topBarTitle, setTopBarTitle] = useState("")
  const [mobileOpen, setMobileOpen] = useState(false) // open status of the side menu in mobile screen

  function handleRobotConnect(id: string) {
    if (id === "useRecentId") {
      networking.socket.close()
      networking = new Networking(networking.lastConnectedId, networking.isConnectedOnce)
    } else {
      networking.socket.close()
      networking = new Networking(id, false)
    }
  }
  function handleRobotDisconnect() {
    networking.socket.close()
  }

  useEffect(() => {
    const id1 = setInterval(() => {
      setForceRerender({})

      // record path when requested
      const latitude = networking.robotState.gps.latitude
      const longitude = networking.robotState.gps.longitude
      pathRecorder.recordWhenRequested(latitude, longitude)
    }, 100)

    return () => {
      clearInterval(id1)
    }
  }, [])

  // console.log("rendered")

  return (
    <Router>
      <CssBaseline />
      <AutoDownhill networking={networking} />
      <TestDataReadyPopUp networking={networking} />
      <TopBar
        networking={networking}
        setMobileOpen={setMobileOpen}
        topBarTitle={topBarTitle}
        pathRecorder={pathRecorder}
        handleRobotConnect={handleRobotConnect}
      />
      <SideDrawer networking={networking} mobileOpen={mobileOpen} setMobileOpen={setMobileOpen} />

      <main className={classes.content}>
        <div className={classes.toolbar} />
        <Switch>
          <Route path="/" exact>
            <Redirect to="/initialsetup" />
          </Route>

          <Route path="/initialsetup">
            <InitialSetup
              networking={networking}
              handleRobotConnect={handleRobotConnect}
              handleRobotDisconnect={handleRobotDisconnect}
              setTopBarTitle={setTopBarTitle}
            />
          </Route>

          <Route path="/manualcontrol">
            <ManualControl
              networking={networking}
              setTopBarTitle={setTopBarTitle}
              pathRecorder={pathRecorder}
            />
          </Route>

          <Route path="/robotstatus">
            <RobotStatus networking={networking} setTopBarTitle={setTopBarTitle} />
          </Route>

          <Route path="/path">
            <Path networking={networking} setTopBarTitle={setTopBarTitle} pathRecorder={pathRecorder} />
          </Route>

          <Route path="/pathfollowing">
            <PathFollowing
              networking={networking}
              setTopBarTitle={setTopBarTitle}
              pathRecorder={pathRecorder}
            />
          </Route>
        </Switch>
      </main>
    </Router>
  )
}
