import React, { useEffect } from "react"
import Joystick from "../components/Joystick"
import Switch from "@material-ui/core/Switch"
import FormControlLabel from "@material-ui/core/FormControlLabel"
import { makeStyles } from "@material-ui/core/styles"
import Button from "@material-ui/core/Button"
import RotateLeftIcon from "@material-ui/icons/RotateLeft"
import RotateRightIcon from "@material-ui/icons/RotateRight"
import ArrowUpwardIcon from "@material-ui/icons/ArrowUpward"
import Box from "@material-ui/core/Box"
import PathRecorder from "../path-recorder"
import Networking from "../networking"

const useStyles = makeStyles((theme) => ({
  spinBox: {
    marginTop: theme.spacing(1.5),
    marginBottom: theme.spacing(1.5),
  },
  moveButton: {
    marginLeft: theme.spacing(2),
  },
  rotateRightButton: {
    marginLeft: theme.spacing(2),
  },
  rotateIcon: {
    marginLeft: theme.spacing(1),
  },
}))

interface Props {
  networking: Networking
  setTopBarTitle: React.Dispatch<React.SetStateAction<string>>
  pathRecorder: PathRecorder
}

let isSpinning = false
let isMovingForward = false

export default function Page({ networking, setTopBarTitle, pathRecorder }: Props) {
  const classes = useStyles()

  const robotState = networking.robotState
  const senders = networking.senders

  function handleManualSwitch(event: React.ChangeEvent<HTMLInputElement>) {
    if (event.target.checked) {
      senders.enableManaul()
    } else {
      senders.stop()
    }
  }

  function handleBrakeWhenStoppedSwitch() {
    senders.toggleBrakeWhenStopped()
  }

  useEffect(() => {
    setTopBarTitle("Manual Control")
  }, [setTopBarTitle])

  return (
    // WebkitUserSelect is used to prevent random text selection when using the joystick
    <div style={{ WebkitUserSelect: "none" }}>
      {/* <p>heading: {((robotState.gps.heading / Math.PI) * 180).toFixed(0)}</p> */}
      <FormControlLabel
        control={
          <Switch checked={robotState.overseerState === 1} onChange={handleManualSwitch} color="primary" />
        }
        label="Enable Manual Control"
        disabled={
          robotState.overseerState === 2 || robotState.overseerState === 3 || robotState.overseerState === 6
        }
      />

      <FormControlLabel
        control={
          <Switch
            checked={robotState.doesBrakeWhenStopped === true}
            onChange={handleBrakeWhenStoppedSwitch}
            color="primary"
          />
        }
        label="Apply Brake when Not Moving"
      />

      <Box className={classes.spinBox}>
        <Button
          variant="outlined"
          color="primary"
          onPointerDown={() => {
            isSpinning = true
            senders.spin(0.3)
          }}
          onPointerUp={() => {
            isSpinning = false
            senders.spin(0)
          }}
          onPointerOut={() => {
            if (isSpinning) {
              senders.spin(0)
            }
          }}
        >
          spin <RotateLeftIcon className={classes.rotateIcon} />
        </Button>

        <Button
          className={classes.rotateRightButton}
          variant="outlined"
          color="primary"
          onPointerDown={() => {
            isSpinning = true
            senders.spin(-0.3)
          }}
          onPointerUp={() => {
            isSpinning = false
            senders.spin(0)
          }}
          onPointerOut={() => {
            if (isSpinning) {
              senders.spin(0)
            }
          }}
        >
          spin <RotateRightIcon className={classes.rotateIcon} />
        </Button>

        <Button
          className={classes.moveButton}
          variant="outlined"
          color="primary"
          onPointerDown={() => {
            isMovingForward = true
            senders.moveForward(0.75)
          }}
          onPointerUp={() => {
            isMovingForward = false
            senders.moveForward(0)
          }}
          onPointerOut={() => {
            if (isMovingForward) {
              senders.moveForward(0)
            }
          }}
        >
          move <ArrowUpwardIcon className={classes.rotateIcon} />
        </Button>
      </Box>

      <Joystick networking={networking} pathRecorder={pathRecorder} />
    </div>
  )
}
