import { makeStyles } from "@material-ui/core/styles"
import Card from "@material-ui/core/Card"
import CardContent from "@material-ui/core/CardContent"
import ErrorOutlineIcon from "@material-ui/icons/ErrorOutline"
import Typography from "@material-ui/core/Typography"
import { MotorController } from "../shared/interfaces"
import { MC_STATE_STRING, NMT_STATE_STRING, MC_ERROR_WORD } from "../shared/constants"

const useStyles = makeStyles((theme) => ({
  title: {
    margin: theme.spacing(1),
  },
  body: {
    margin: theme.spacing(1),
  },
  errorIcon: {
    // marginLeft: theme.spacing(1.5),
    verticalAlign: "-3px",
    color: "red",
    fontSize: "17px",
  },
}))

interface Props {
  motorController: MotorController
  id: number
  location: string
}

export default function Component({ motorController, id, location }: Props) {
  const classes = useStyles()

  return (
    <Card variant="outlined">
      <CardContent>
        <Typography variant="h5" className={classes.title}>
          Motor Controller {id}
        </Typography>

        <Typography variant="body1" className={classes.title}>
          Motor Location: {location}
        </Typography>

        <Typography variant="body1" className={classes.body}>
          Error: {MC_ERROR_WORD[motorController.errorWord]}{" "}
          {motorController.errorWord !== 0 ? <ErrorOutlineIcon className={classes.errorIcon} /> : null}
        </Typography>

        <Typography variant="body1" className={classes.body}>
          Heartbeat Timeout: {motorController.isHeartbeatTimeout.toString()}{" "}
          {motorController.isHeartbeatTimeout ? <ErrorOutlineIcon className={classes.errorIcon} /> : null}
        </Typography>

        <Typography variant="body1" className={classes.body}>
          State: {MC_STATE_STRING[motorController.state]}
        </Typography>

        <Typography variant="body1" className={classes.body}>
          NMT State: {NMT_STATE_STRING[motorController.heartbeatNmt]}
        </Typography>

        <Typography variant="body1" className={classes.body}>
          Motor Current (A): {motorController.motorCurrentDraw.toFixed(2)}
        </Typography>

        <Typography variant="body1" className={classes.body}>
          Actual Wheel RPM: {motorController.wheelRpmActual.toFixed(0)}
        </Typography>

        <Typography variant="body1" className={classes.body}>
          Winding Temperature (F): {motorController.windingTemperature.toFixed(0)}
        </Typography>
      </CardContent>
    </Card>
  )
}
