import React, { useEffect, useState } from "react"

import { makeStyles } from "@material-ui/core/styles"

import Card from "@material-ui/core/Card"
import CardContent from "@material-ui/core/CardContent"
import Snackbar from "@material-ui/core/Snackbar"
import MuiAlert from "@material-ui/lab/Alert"
import InfoIcon from "@material-ui/icons/Info"
import IconButton from "@material-ui/core/IconButton"
import Modal from "@material-ui/core/Modal"
import Typography from "@material-ui/core/Typography"

import Networking from "../networking"

const useStyles = makeStyles((theme) => ({
  TempInfoButton: {
    paddingBottom: 1,
    paddingTop: 0,
    marginLeft: theme.spacing(1),
    color: "white",
  },
  TempInfoCard: {
    width: 400,
  },
  snackbar: {
    opacity: 0.85,
  },
}))

interface Props {
  networking: Networking
}

export default function Component({ networking }: Props) {
  const classes = useStyles()

  const [maxMotorTemp, setMaxMotorTemp] = useState(0)
  const [snackbarOpen, setSnackbarOpen] = useState(false)
  const [modalOpen, setModalOpen] = useState(false)
  const [maxRobotTemp, setMaxRobotTemp] = useState(0)

  // handleMotorTemperature()
  useEffect(() => {
    const motor_temp_threshold = 185
    const robot_temp_threshold = 140

    const mcs = networking.robotState.motorControllers
    const robot = networking.robotState
    const max_t = Math.max(
      mcs.leftBack.windingTemperature,
      mcs.leftFront.windingTemperature,
      mcs.rightBack.windingTemperature,
      mcs.rightFront.windingTemperature
    )
    
    const max_robot_t = Math.max(
      robot.batteryTemperature,
      robot.robotTemperature
    )

    setMaxMotorTemp(max_t)
    setMaxRobotTemp(max_robot_t)

    if (max_t > motor_temp_threshold || max_robot_t > robot_temp_threshold) {
      setSnackbarOpen(true)
      } else {
      setSnackbarOpen(false)
      }
  }, [setMaxMotorTemp, networking.robotState])

  // Temp thresholds constants
  const motor_threshold_temp = 185
  const robot_threshold_temp = 140

  // If statements for different snackbar popups/dialogue
  if (maxMotorTemp > motor_threshold_temp && maxRobotTemp > robot_threshold_temp) {
    return (
      <div>
        <Modal
          style={{ display: "flex", alignItems: "center", justifyContent: "center" }}
          open={modalOpen}
          onClose={() => setModalOpen(false)}
        >
          <Card className={classes.TempInfoCard} variant="outlined">
            <CardContent>
              <Typography variant="body1">
              Both the motor winding temperature and robot/battery temperature are approaching temperature thresholds. <br/>
              Upon the robot/battery temperature reaching the temperature threshold, robot operation will be disabled. <br/>
              Upon the motor winding temperature reaching its threshold, STRIDE will enter descender mode if it is on a hill, 
              unless E-stopped, where it will hold until some more critical temperature threshold is reached. <br/>
              Avoid leaving the STRIDE E-stopped on a hill whenever possible. <br/>
              </Typography>
            </CardContent>
          </Card>
        </Modal>
        
        <Snackbar className={classes.snackbar} open={snackbarOpen}>
          <MuiAlert elevation={6} variant="filled" severity="warning">
            Motor Temp: {maxMotorTemp} / 185F
            <IconButton
              className={classes.TempInfoButton}
              size="small"
              onClick={() => {
                setModalOpen(true)
              }}
            >
              <InfoIcon />
            </IconButton>
            <br/> Robot Temp: {maxRobotTemp} / 140F <br/>
          </MuiAlert>
        </Snackbar>
      </div>
    )
  }
  else if (maxMotorTemp > motor_threshold_temp && maxRobotTemp <= robot_threshold_temp) {
    return (
      <div>
        <Modal
          style={{ display: "flex", alignItems: "center", justifyContent: "center" }}
          open={modalOpen}
          onClose={() => setModalOpen(false)}
        >
          <Card className={classes.TempInfoCard} variant="outlined">
            <CardContent>
              <Typography variant="body1">
              Upon the motor winding temperature reaching its threshold, STRIDE will enter descender mode if it is on a hill, 
              unless E-stopped, where it will hold until some more critical temperature threshold is reached. <br/>
              Avoid leaving the STRIDE E-stopped on a hill whenever possible. <br/> 
              </Typography>
            </CardContent>
          </Card>
        </Modal>
        
        <Snackbar className={classes.snackbar} open={snackbarOpen} anchorOrigin={{ vertical: "bottom", horizontal: "center" }}>
          <MuiAlert elevation={6} variant="filled" severity="warning">
            Motor Temp: {maxMotorTemp} / 185F
            <IconButton
              className={classes.TempInfoButton}
              size="small"
              onClick={() => {
                setModalOpen(true)
              }}
            >
              <InfoIcon />
            </IconButton>
          </MuiAlert>
        </Snackbar>
      </div>
    )
  }

  else if (maxMotorTemp <= motor_threshold_temp && maxRobotTemp > robot_threshold_temp) {
    return (
      <div>
        <Modal
          style={{ display: "flex", alignItems: "center", justifyContent: "center" }}
          open={modalOpen}
          onClose={() => setModalOpen(false)}
        >
          <Card className={classes.TempInfoCard} variant="outlined">
            <CardContent>
              <Typography variant="body1">
                Upon the robot/battery temperature reaching the temperature threshold, robot operation will be disabled. <br/>
              </Typography>
            </CardContent>
          </Card>
        </Modal>

        <Snackbar className={classes.snackbar} open={snackbarOpen} anchorOrigin={{ vertical: "bottom", horizontal: "center" }}>
          <MuiAlert elevation={6} variant="filled" severity="warning">
            Robot Temp: {maxRobotTemp} / 140F
            <IconButton
              className={classes.TempInfoButton}
              size="small"
              onClick={() => {
                setModalOpen(true)
              }}
            >
              <InfoIcon />
            </IconButton>
          </MuiAlert>
        </Snackbar>
      </div>
    )
  }

else {
  return null
}
}
